<template>
  <div>
    <h2>商品添加</h2>
    <div>
      <!-- 商品名称 -->
      <p>
        <label>商品名称</label>
        <el-input placeholder="商品名称"
                  v-model="productName"
                  clearable>
        </el-input>
      </p>
      <!-- 商品分类 -->
      <p>
        <label>商品分类</label>
        <el-select v-model="value"
                   placeholder="请选择商品分类"
                   filterable
                   allow-create>
          <el-option v-for="item in products"
                     :key="item.value"
                     :label="item.label"
                     :value="item.value">
            <span style="float: left">{{ item.label }}</span>
            <span style="float: right; color: #8492a6; font-size: 13px">{{ item.value }}</span>
          </el-option>
        </el-select>
      </p>
      <p>
        <label>商品价格</label>
        <el-input-number size="medium"
                         v-model="num"
                         :min="0"
                         :precision="2"
                         :step="0.1"></el-input-number>
      </p>
      <p>
        <label>商品图片</label>
        <!-- 商品上传 -->
        <el-upload class="avatar-uploader"
                   action="http://127.0.0.1:5000/goods/goods_img_upload"
                   :show-file-list="false"
                   :on-success="handleAvatarSuccess">
          <img v-if="imageUrl"
               :src="imgs"
               class="avatar">
          <i v-else
             class="el-icon-plus avatar-uploader-icon"></i>
        </el-upload>

      </p>
      <p>
        <label>商品描述</label>
        <el-input type="textarea"
                  :autosize="{ minRows: 2, maxRows: 3}"
                  v-model="textarea"
                  clearable
                  size="medium">
        </el-input>
      </p>
    </div>
    <el-button type="primary"
               @click="orderAdd()">添加商品</el-button>
  </div>
</template>

<script>
import { addorder, ITEMS_UP_IMG, GET_IMG } from '@/API/apis'

export default {
  data () {
    return {
      imageUrl: '',
      num: 0,
      textarea: '',
      productName: '',
      value: '',
      ITEMS_UP_IMG: '',
      GET_IMG: '',
      imgs: "",
      products: [
        {
          value: '新品专享',
          label: '新品专享'
        },
        {
          value: '特色专区',
          label: '特色专区'
        }
      ]
    };
  },
  create () {
    this.value = this.label
    this.ITEMS_UP_IMG = ITEMS_UP_IMG
    this.GET_IMG = GET_IMG
  },
  methods: {
    handleAvatarSuccess (res) {
      this.imageUrl = res.imgUrl
      this.imgs = "http://127.0.0.1:5000/upload/imgs/goods_img/" + res.imgUrl
    },

    orderAdd () {
      addorder(this.productName, this.value, this.num, this.imageUrl, this.textarea).then(res => {
        // console.log(res);
        if (res.data.code == 0) {
          this.$message({
            message: '恭喜你，这是一条成功消息',
            type: 'success'
          });
          this.productName = ""
          this.value = ""
          this.num = ""
          this.imageUrl = ""
          this.textarea = ""
        }
      })
    }
  }
}
</script>

<style lang="less" scoped>
h2 {
  margin-bottom: 30px;
}
p {
  padding-bottom: 25px;
  .el-input,
  .el-select {
    width: 217px;
  }
  label {
    margin-right: 20px;
    color: #808080;
  }
  // 图片上传部分
  .avatar-uploader-icon {
    font-size: 28px;
    color: #909090;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
    margin-left: 115px;
    border: 1px solid #cccccc;
  }
}
img {
  width: 150px;
  height: 150px;
}
</style>